@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
input {
  all: unset;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
}

body {
  background-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.post-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.post-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.post-input::placeholder {
  color: white;
  opacity: 0.9;
}

.container {
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
}

.formInput {
  width: 100%;
  padding: 10px 20px;
  border-radius: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: white;
  color: black;
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 7px 20px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  cursor: pointer;
}

.cancelBtn {
  cursor: pointer;
  background-color: tomato;
}

/* Profile Page */

.profileForm {
  border-bottom: 1px solid rgba(255, 255, 255, 0.9);
  padding-bottom: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.logOut {
  margin-top: 50px;
}

/* Nwitter Factory */

.factoryForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.sourceForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
    width: 100%;
  }

.factoryInput__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: relative;
  margin-bottom: 20px;
  width: 100%;
}

.factoryInput__input {
  flex-grow: 1;
  height: 40px;
  padding: 0px 20px;
  color: white;
  border: 1px solid #04aaff;
  border-radius: 20px;
  font-weight: 500;
  font-size: 12px;
}

.URL_TXT_Input__input {
    flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    color: white;
    border: 1px solid #04aaff;
    border-radius: 10px;
    font-weight: 500;
    font-size: 12px;
  }

.URL_Input__input {
    flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    color: black;
    background-color: white;
    border: 1px solid #04aaff;
    border-radius: 10px;
    font-weight: 500;
    font-size: 12px;
  }

.TXT_Input__input {
flex-grow: 1;
height: 100px;
padding: 0px 20px;
color: black;
border: 1px solid #04aaff;
border-radius: 10px;
font-weight: 500;
font-size: 12px;
}

.factoryInput__arrow {
  position: absolute;
  right: 0;
  background-color: #04aaff;
  height: 40px;
  width: 40px;
  padding: 10px 0px;
  text-align: center;
  border-radius: 20px;
  color: white;
  box-sizing: border-box;
}

.URL_Input__arrow {
    position: absolute;
    right: 0;
    background-color: #04aaff;
    height: 40px;
    width: 40px;
    padding: 10px 0px;
    text-align: center;
    border-radius: 10px;
    color: white;
    box-sizing: border-box;
  }

.TXT_Input__arrow {
position: absolute;
right: 0;
background-color: #04aaff;
height: 100px;
width: 40px;
padding: 10px 0px;
text-align: center;
border-radius: 10px;
color: white;
box-sizing: border-box;

}

.factoryInput__label {
  color: #04aaff;
  cursor: pointer;
}

.factoryInput__label span {
  margin-right: 10px;
  font-size: 12px;
}

.Input__label {
    color: #04aaff;
    cursor: pointer;
    margin: 10px 20px 10px 20px;
  }
  
.Input__label span {
margin-right: 10px;
font-size: 12px;
}

.factoryUpdate__label {
    color: #04aaff;
    cursor: pointer;
    text-align: center;
    
  }
.factoryUpdate__label span {
    margin-right: 10px;
    font-size: 12px;    
  }
.factoryForm__attachment {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.factoryForm__attachment img {
  height: 200px;
  width: 200px;
  border-radius: 10px;
}

.factoryForm__clear {
  color: #04aaff;
  cursor: pointer;
  text-align: center;
}

.factoryForm__clear span {
  margin-right: 10px;
  font-size: 12px;
}

/* post */

.postEdit .formBtn {
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 5px;
}

.post {
  margin-bottom: 20px;
  background-color: white;
  width: 100%;
  max-width: 320px;
  padding: 20px;
  border-radius: 10px;
  position: relative;
  display: flex;
  flex-direction: column;
  color: rgba(0, 0, 0, 0.8);
  align-items: center;
}

.post h4 {
  font-size: 14px;
}

.post img {
  right: -10px;
  top: 20px;
  border-radius: 10%;
  width: 100px;
  height: 100px;
  margin-top: 10px;
}

.post__actions {
  position: absolute;
  right: 10px;
  top: 10px;
}

.post__actions span {
  cursor: pointer;
}

.post__actions span:first-child {
  margin-right: 10px;
}

/* Auth */

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
}

/* AuthForm */

.authInput {
  max-width: 320px;
  width: 100%;
  padding: 10px;
  border-radius: 30px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 10px;
  font-size: 12px;
  color: black;
  box-sizing: content-box;
}

.authSubmit {
  text-align: center;
  background: #04aaff;
  color: white;
  margin-top: 10px;
  box-sizing: content-box;
  cursor: pointer;
}

.authError {
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: #04aaff;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 50px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

.footer {
    position: fixed;
    margin-top: auto;
    bottom: 0;
    width: 100%;
    height: 70px;
}
